import React from 'react';
import styled from 'styled-components';
import Logon from './Logon';
import FooterBlock from './FooterBlock';
import PresentBlock from './PresentBlock';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

const Wraper = styled.div`
    width:100vw;
    height:100vh;
    background-color:#fcfcfc;
    z-index:100;
    display:grid;
    grid-template-columns:1fr;
    grid-template-rows:700px 100px 40px auto auto auto 60px;
    overflow-y:auto;
    overflow-x:hidden;
`
const Img = styled.div`
    width:100vw;
    height:700px;
    background-size:cover;
    background-repeat:no-repeat;
`

const Footer = styled.div`
    display:grid;
    justify-content: center;
    align-content:center;
    grid-template-columns:repeat(auto-fit,320px);
    grid-row-gap:5px;
    grid-column-gap:30px;
    padding:10px;
    grid-template-rows:1fr;
`

const Headline = styled.div`
    color:black;
    font-size:36px;
    font-weight:bold;
    display:grid;
    align-content:center;
    justify-content:center;
`

const Line = styled.div`
    width:320px;
    height:2px;
    margin:auto;
    border-bottom:2px solid rgb(16,32,107);
`
const BR = styled.div`
    height:100px;
`

const InformationPost=styled.div`
    width:100vw;
    background-color:rgb(16,32,107);
    display:grid;
    justify-content:space-around;
    align-content:center;
    grid-gap:20px;
    padding:10px;
    grid-template-columns:repeat(auto-fit,320px);
    grid-template-rows:repeat(auto-fit,320px);
`

const FooterLine=styled.div`
    height:auto;
    width:100vw;
    background-color:rgb(16,32,107);
    box-shadow: 0 20px 38px rgba(0, 0, 0, 0.16);
    color:white;
    font-size:20px;
    display:grid;
    align-content:center;
    justify-content:center;
    padding:10px 0;
    z-index:100000;
`

const Div=styled.div`
    width:100vw;
    background-color:rgb(16,32,107);
`

class FrontPage extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
         if(this.props.store.user && this.props.store.user.email){
             window.location.replace('#/user/Image');
         }
        return (
            <Wraper>
                <Img>
                    <Logon></Logon>
                </Img>
                <Headline>{this.props.store.language.firstTimeWithUs}</Headline>
                <Line />
                <Footer>
                    <Link to={`/Client`}><FooterBlock source={"https://image.ibb.co/dTmdUH/bbb.jpg"} title={this.props.store.language.client} text={this.props.store.language.clientText} /></Link>
                    <Link to={`/Firm`}><FooterBlock source={"http://www.mineks.com/images/pages/22935city_scape5b.jpg"} title={this.props.store.language.interprise} text={this.props.store.language.interpriseText} /></Link>
                    <Link to={`/Integration`}><FooterBlock source={"https://image.ibb.co/mPrTaS/rtyrtyrtydfvdfv.jpg"} title={this.props.store.language.integration} text={this.props.store.language.integrationText} /></Link>
                </Footer>
                <FooterLine>
                    {this.props.store.language.gladToCoop}
                </FooterLine>
                <InformationPost>
                    <img src="https://image.ibb.co/nC6WTc/img1.png" width="320px"/>
                    <PresentBlock email="Ba@brados.by" name={this.props.store.language.stas} title={this.props.store.language.leader}/>
                    <img src="https://image.ibb.co/ioqhZS/hhhhhhhh.png" width="320px"/>
                </InformationPost>
                <Div/>
            </Wraper>
        )
    }
}
export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
    })
)(FrontPage);

