import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import Button from './Button';

const Wraper = styled.div`
    width:380px;
    height:300px;
    display:grid;
    grid-template-rows:40px 220px 40px;
    grid-template-columns:330px 50px;
    grid-template-areas: 
            "top top top"
            "center center side"
            "foot foot side";
    color:white;
    font-size:15px;
    border-radius:10px;
    margin:0 auto;
    transition:height 0.3s;
`

const Head = styled.div`
    width:380px;
    height:40px;
    grid-area:top;
    border-top-left-radius:10px;
    border-top-right-radius:10px;
    display:grid;
    padding:10px;
    padding-top:0;
    border-bottom:2px solid black;
    background-color:rgb(16,32,107);
`

const Center = styled.div`
    width:330px;
    height:220px;
    grid-area:center;
    background-color:rgb(16,32,107);
    display:grid;
    justify-content:center;
    align-content:center;
`

const Side = styled.div`
    width:50px;
    height:260px;
    grid-area:side;
    margin-left:-50px;
    border-bottom-right-radius:10px;
    border-left:2px solid black;
    background-color:rgb(16,32,107);
    display:grid;
    justify-content:center;
    align-content:space-around;
    grid-template-rows:40px 40px 40px;
`

const Foot = styled.div`
    width:330px;
    height:40px;
    grid-area:foot;
    border-bottom-left-radius:10px;
    border:2px solid rgb(16,32,107);
    border-top:2px solid black;
    border-right:0;
    background-color:rgba(16,32,107,0.5);
    display:grid;
    justify-content:space-around;
    align-content:center;
    grid-template-columns:120px 120px;
`

const MenuButton = styled.img`
    width:25px;
    filter:invert(100%);
`
const ButtonWrap = styled.div`
    height:40px;
    width:40px;
    justify-self:flex-end;
    align-self:center;
    display:grid;
    justify-content:center;
    align-content:center;
`

const InfoVerify = styled.div`
    width:45px;
    height:50px;
    border-top:3px solid black;
    border-bottom:3px solid black;
    background-color:rgba(255,255,255,1);
    margin-top:-5px;
    display:grid;
    justify-content:center;
    align-content:center;
    font-weight:600;
    font-size:15px;
    color:black;
`

const Img = styled.img`
    filter:invert(100%);
    height:30px;
    background-color:rgba(0,0,0,0.5);
    border-radius:50%;
    justify-self:center;
    padding-left:3px;
`

const Image = styled.img`
    max-height:210px;
    max-width:320px;
`

const Menu = styled.div`
    width:150px;
    height:34px;
    background-color:white;
    z-index:1000;
    position:relative;
    left:-50px;
    top:0px;
    border:2px solid grey;
`

const MenuItem = styled.div`
    height:30px;
    width:146px;
    ${MenuItem}:hover{
        background-color:#ddd;
    }
    color:black;
    display:grid;
    justify-content:center;
    align-content:center;
    font-weight:500;
`
const Percent = styled.div`
    font-size:25px;
    font-family:Courier New;
    font-weight:600;
    color:${props => props.color};
`

const RED = "https://image.ibb.co/hgn76T/Red.png";
const GREEN = "https://image.ibb.co/iW8x6T/Green.png";
const GREY = "https://image.ibb.co/hkRpLo/Grey.png";

class DocBlock extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            interpol: props.interpol,
            officer: props.officer,
            per: props.per,
            src: props.src,
            menuDrop: false,
            id:props.id,
        };
        this.updateView=this.updateView.bind(this);
    }
    menuDrop() {
        this.setState({ menuDrop: !(this.state.menuDrop) });
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            ...nextProps,
        });
    }
    updateView(){
        let ind=this.props.store.arrayPhoto.findIndex((e)=> e.id===this.state.id);
        this.props.onUpdateView(this.props.store.arrayPhoto[ind].data);
    }
    render() {
        let colorStr;
        let color = Math.ceil(this.state.per * 2.55);
        colorStr = `rgb(${255 - color},${color},0)`;
        return (
            <Wraper>
                <Head>
                    <ButtonWrap onClick={this.menuDrop.bind(this)}>
                        {!(this.state.menuDrop) && <MenuButton src="https://image.ibb.co/dcnEjH/img_151687.png" />}
                        {this.state.menuDrop && <Menu id={`${this.id}`} onClick={this.menuDrop.bind(this)}>
                            <MenuItem>{this.props.store.language.delete}</MenuItem>
                        </Menu>}
                    </ButtonWrap>
                </Head>
                <Center>
                    <Image src={this.state.src} />
                </Center>
                <Foot>
                    <Button width="120px">{this.props.store.language.side1}</Button>
                    <Button width="120px">{this.props.store.language.side2}</Button>
                </Foot>
                <Side>
                    <InfoVerify title={this.props.store.language.percent}>
                        <Percent color={colorStr}>
                            {this.state.per && `${this.state.per}%`}
                        </Percent>
                    </InfoVerify>
                    <InfoVerify title={this.props.store.language.officer}>
                        <img width="40px" height="40px" src={this.state.officer === true ? GREEN : this.state.officer === false ? RED : GREY} />
                    </InfoVerify>
                    <InfoVerify title={this.props.store.language.interpoll}>
                        <img width="40px" height="40px" src={this.state.interpol === true ? GREEN : this.state.interpol === false ? RED : GREY} />
                    </InfoVerify>
                    <Img src="https://image.ibb.co/g9FSoc/icon_ios7_arrow_forward_512.png" title={this.props.store.language.moreInfo} onClick={this.updateView}/>
                </Side>
            </Wraper>
        )
    }
}
export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
        onUpdateView: (data) => {
            dispatch({ type: 'UPDATE_DOCUMENT_VIEW',payload:data});
        },
    })
)(DocBlock);


