import React from 'react';
import styled from 'styled-components';
import Logon from './Logon';
import { connect } from 'react-redux';

const Wraper = styled.div`
    width:320px;
    height:200px;
    display:grid;
    grid-gap:10px;
    grid-template-rows:30px 2px 20px 20px 20px 20px;
    color:white;
    font-size:15px;
`

const Title = styled.div`
    width:100%;
    font-weight:400;
    font-size:24px;
    display:grid;
    align-content:center;
    justify-content:center;
`
const BR = styled.div`
    width:100%;
    height:2px;
    background-color:white;
`
const Text = styled.div`
    display:grid;
    font-size:15px;
    font-weight:400;
    align-content:center;
`

const Former=styled.div`
    display:grid;
    grid-template-columns:20px auto;
`

const Img=styled.img`
    width:20px;
    filter:invert(100%);
`

class PresentBlock extends React.Component {
    constructor(props) {
        super(props)
        this.props = props;
    }
    render() {
        const props=this.props;
        return (
            <Wraper>
                <Title>{props.title}</Title>
                <BR />
                <Text>{props.name}</Text>
                <Former><Img src="https://image.ibb.co/jQ5i8c/mail_email_message_electronic_online_web_icon_icons_com_59986.png"/><Text>{` ${this.props.store.language.email}: ${props.email}`}</Text></Former>
            </Wraper>
        )
    }
}
export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
    })
)(PresentBlock);

