import React from 'react';
import styled from 'styled-components'
import { connect } from 'react-redux';

const SPRITE="https://image.ibb.co/mOB1Jc/payments_n.png";

const Wraper=styled.div`
  width:100vw;
  height:100vh;
  background-color:white;
  display:grid;
  align-content: center; 
  justify-content:center;
  grid-template-rows:230px 500px;
`

const Bill=styled.div`
    width:100vw;
    height:130px;
    display:grid;
    grid-template-columns: 230px 230px;
    grid-gap:10px;
    justify-content:space-around;
`

const InfoBox=styled.div`
    border:3px solid rgb(16,32,107);
    border-radius:5px;
    width:230px;
    height:130px;
    display:grid;
    grid-template-rows: 64px 2px 64px;
    color:white;
    background-color:rgb(16,32,107);
`

const MoneyUp=styled.div`
    margin 0 auto;
    height:500px;
    width:320px;
    background-color:#f5f5f5;
    display:grid;
    grid-template-rows: 80px 1fr;
`
const UpMenu=styled.div`
    height:80px;
    display:grid;
    grid-template-columns:repeat(auto-fit,80px);
`

const Icon=styled.div`
    height:42px
    width:42px;
    background-image:url(${SPRITE});
    background-position:${(props)=>props.x}px ${(props)=>props.y}px;
`

const Butt=styled.div`
    height:80px
    width:80px;
    display:grid;
    align-content: center; 
    justify-content:center;
    background-color:#f5f5f5;
    ${Butt}:hover{
        background-color:#e0e0e0;
    }
`

const Text=styled.div`
    font-weight:500;
    font-size:30px;
    display:grid;
    align-content: center; 
    justify-content:center;
`

const BR=styled.div`
    height:2px;
    width:100%;
    background-color:white;
`

class Billing extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <Wraper>
                <Bill>
                    <InfoBox>
                        <Text>{this.props.store.language.count}</Text>
                        <BR/>
                        <Text>{this.props.store.bill.count}</Text>
                    </InfoBox>
                    <InfoBox>
                        <Text>{this.props.store.language.days}</Text>
                        <BR/>
                        <Text>{this.props.store.bill.days}</Text>
                    </InfoBox>
                </Bill>
                <MoneyUp>
                    <UpMenu>
                        <Butt><Icon x={"0"} y={"0"}/></Butt>
                        <Butt><Icon x={"0"} y={"-42"}/></Butt>
                        <Butt><Icon x={"0"} y={"-84"}/></Butt>
                        <Butt><Icon x={"0"} y={"-126"}/></Butt>
                    </UpMenu>
                </MoneyUp>
            </Wraper>
        )
    }
}

export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
    })
)(Billing);


