import React from 'react';
import styled from 'styled-components';
import Result from './Result';
import Billing from './Billing';
import Button from './ComponentButton';
import { HashRouter as Router, Route, Switch, Link } from 'react-router-dom';
import { connect } from 'react-redux';

const classList = {
    Result,
    Billing,
};


const PHOTOSRC="https://image.ibb.co/dBWeoc/photo_video_slr_camera_icon.png";
const UPLOADSRC="https://image.ibb.co/iOwquH/very_basic_folder_icon.png";
const BILLSRC="https://image.ibb.co/dgGJgx/ecommerce_collection_outlines_28_512.png";

const Wraper = styled.div`
    width:100vw;
    height:100vh;
    left:0;
    top:0;
    overflow:hidden;
    background-color:#fff;
    display:grid;
    align-items: stretch;
    grid-template-columns:72px calc(100% - 72px);
    grid-template-areas:
    "menu route";
    @media (max-width: 500px) {
        grid-template-rows:1fr;
        grid-template-columns:1fr;
        grid-template-areas:
        "route";
    }
`;

const ButtonWrap = styled.div`
    height:calc(100vh - 70px);
    left:0;
    display:grid;
    grid-template-rows:auto 72px;
    grid-row-gap:10px;
    padding:10px 0 0 0;
    @media (max-width: 500px) {
        width:100vw;
        height:150px;
        transition:top 0.3s ;
        justify-content:center;
        align-content:space-around;
        grid-template-columns:1fr 72px;
        grid-template-rows:100px;
        position:absolute;
        top:-150px;
        z-index:1000;
    }
    @media (max-height: 500px) {
        grid-template-rows:auto 72px;
    }
    background-color:#f0f0f0;
    grid-area:menu;
`;

const ButtonList = styled.ul`
    width:100%;
    display:grid;
    justify-items: center;
    grid-template-rows:repeat(3,96px);
    grid-gap:10px;
    list-style-type: none;
    @media (max-width: 500px) {
        grid-template-columns:repeat(4,66px);
        grid-template-rows:96px;
        grid-gap:10px;
        align-content:center;
        justify-content: space-around;
    }
    padding-left:0;
    background-color:#f0f0f0;
    @media (max-height: 500px) {
        margin-bottom:0;
        padding-top:0%;
    }
`;

const WraperRoute = styled.div`
    grid-area:route;
    @media (max-width: 500px) {
        width:100vw;
    }
`;


const Center = styled.div`
    border-top:2px solid black;
    width:72px;
    display:grid;
    align-content: center; 
    justify-content:center;
    @media (max-height: 500px) {
        align-content: start;
    }
    @media(max-width:500px){
        border-top:0;
    }
    @media (max-height: 400px) {
        display:none;
    }
`
const WraperMainMenu = styled.div`
    position:absolute;
    width:100%;
    height:100%;
    padding-top:30vh;
    justify-content:center;
    background-color:rgba(120,120,120,0.5);
    z-index:9999;
`

const MainMenu = styled.div`
    width:320px;
    height:150px;
    display:grid;
    grid-template-columns:1fr;
    grid-template-rows:60px 30px 30px 30px;
    border:1px solid black;
    background-color:#fff;
    z-index:10000;
`

const MenuHeader = styled.div`
    font-family: Brush Script MT, Brush Script Std, cursive;
    font-weight:500;
    font-size:25px;
    border-bottom:1px solid black;
    display:grid;
    justify-content:center;
    align-content:center;
`

const MenuButton = styled.div`
    border-bottom:1px solid black;
    display:grid;
    justify-content:center;
    align-content:center;
    ${MenuButton}:hover{
        background-color:#ddd;
    }
`

const Input=styled.input`
    width:66px;
    height:96px;
    cursor:pointer;
    position:absolute;
    opacity:0;
    z-index:100000;
`

class ButtonMenu extends React.Component {
    constructor(props) {
        super(props);
        this.popUp = false;
        const UserMenuButton = styled.div`
            width:60px;
            height:60px;
            background-size: contain;
            background-image: url(https://www.evernote.com/redesign/business/BusinessHomeAction/letters/${(this.props.store.user && this.props.store.user.firstName && this.props.store.user.firstName.substring(0, 1).toLowerCase()) || 'c'}_100px_1.png);
            background-repeat: no-repeat;
            background-position:bottom;
            border-radius:100%;
            transition:transform 0.3s;
            @media (max-height: 500px) {
                width:65px;
                height:65px;
            }
      `
        this.usertag = UserMenuButton;
        this.mainMenuDrop = props.menuDrop.bind(this);
        this.mayDown = this.mayDown.bind(this);
        this.visible = this.visible.bind(this);
    }
    mayDown() {
        if (this.popUp) {
            this.mainMenuDrop();
        }
    }
    visible() {
        let elem = document.getElementById("usersharm");
        elem.style.transform = "rotate(90deg)";
        setTimeout(() => { elem.style.transform = "rotate(0deg)"; }, 300);
        let menu = document.getElementById("mainMenu");
        if (menu.style.display === "none") {
            menu.style.display = "grid";
        }
        else {
            menu.style.display = "none";
        }
        this.mayDown();
    }
    componentDidMount(){
        function handleFileSelect(evt) {
            let files = evt.target.files;
            for(let i=0;i<files.length;i++){
                let reader = new FileReader();
                reader.onload =  ()=> {
                    this.props.onPush(reader.result);
                };
                reader.readAsDataURL(files[i]);
            }
        }
        document.getElementById('files').addEventListener('change', handleFileSelect.bind(this), false);
    }
    UploadClick(){
        window.location.replace("#/User/Result");
        document.getElementById("ButtonUpload").click();
    }
    MouseOver(){
        let elem=document.getElementById("ButtonUpload");
        let title=elem.childNodes[1];
        elem.style.backgroundColor="rgba(16,32,107,0.2)";
        title.style.backgroundColor="rgb(16,32,107)";
        title.style.color="white";
    }
    MouseOut(){
        let elem=document.getElementById("ButtonUpload");
        let title=elem.childNodes[1];
        elem.style.backgroundColor="white";
        title.style.backgroundColor="white";
        title.style.color="rgb(16,32,107)";
    }
    render() {
        return (
            <Wraper id="Wraper">
                <ButtonWrap id="buttonWrap">
                    <ButtonList>
                    <div onMouseOver={this.MouseOver} onMouseOut={this.MouseOut}><Input type="file" id="files" name="files[]" accept="image/*" multiple onClick={this.UploadClick}/><Link  to="/User/Result" onClick={this.mainMenuDrop}><Button id="ButtonUpload" content={this.props.store.language.upload} imageSource={UPLOADSRC}/></Link></div>
                        <Link to="/User/Result" onClick={()=>{this.mainMenuDrop();this.props.ON();}}><Button id="Snapshot" content={this.props.store.language.photo} imageSource={PHOTOSRC}/></Link>
                        <Link to="/User/Billing" onClick={this.mainMenuDrop}><Button id="Billing" content={this.props.store.language.billing} imageSource={BILLSRC}/></Link>
                    </ButtonList>
                    <Center>
                        <this.usertag id="usersharm" onClick={this.visible}/>
                    </Center>
                </ButtonWrap>
                <WraperRoute id="wraperRoute" onClick={this.mayDown}>
                    <Router>
                        <Route path="/User/:tab" render={(props) => {
                            let { tab } = props.match.params;
                            return <Tab tab={tab} data={this.data} />
                        }} />
                    </Router>
                </WraperRoute>
                <WraperMainMenu id="mainMenu" style={{ display: "none" }} onClick={this.visible}>
                    <MainMenu onClick={(e) => { e.stopPropagation(); }}>
                        <MenuHeader>{"this.props.store.user.email"}</MenuHeader>
                        <MenuButton onClick={this.changePassword}>Change Password</MenuButton>
                        <MenuButton onClick={this.props.onLogoff}>Log off</MenuButton>
                        <MenuButton onClick={this.visible}>Cancel</MenuButton>
                    </MainMenu>
                </WraperMainMenu>
            </Wraper>
        )
    }
}
const Tab = (props) => {
    var { tab } = props;
    var MyComponent = classList[tab];
    let a = document.getElementById("gridplace");
    if (a && a.style.display === "none") {
        a.style.display = "grid";
    }
    return <MyComponent data={props.data} />;
}
export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
        onLogoff: () => {
            const asyncSetData = () => {
                return (dispatch) => {
                    let xhr = new XMLHttpRequest();
                    xhr.open('POST', '/api/account/logoff', false);
                    xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
                    xhr.onload=()=>{
                        let datas=JSON.parse(xhr.responseText);
                        if(datas.success){
                        window.location.replace("#/");
                        }
                    }
                    xhr.send();
                }
            }
            dispatch(asyncSetData());
        },
        onPush: (data) => {
            dispatch({type:'PUSH',payload:data});
        },
        ON: () => {
            dispatch({ type: 'ON' });
        },
    })
)(ButtonMenu);


