import { eVisualFieldType, GraphicFieldTypes, eRPRM_ResultType, eRPRM_FieldVerificationResult } from './Enums.js';
import { Strings } from './Strings.js';

export default function parsing(arr) {
    let ans={};
    for (let i = 0; i < arr.length; i++) {
        if (arr[i].index === 15) {
            ans.lexical=ParseLexicalData(arr[i].doc);
        }
        if (arr[i].index === 6) {
            ans.photo=ParsePhotoData(arr[i].doc);
        }
        if (arr[i].index === 9) {
            ans.type=ParseDocData(arr[i].doc);
        }
    }
    return ans;
}

function GetTranslation(codeToTranslate) {
    let result = Strings[codeToTranslate];
    if (result == "undefined") {
        result = "";
    }
    return result;
}

function getFromEnum(set, value) {
    for (let k in set) {
        if (set.hasOwnProperty(k)) {
            if (set[k] == value) {
                return k;
            }
        }
    }
    return "";
}

function ParseDocData(xmlDoc) {
    let arr=[];
    var nodes = xmlDoc.getElementsByTagName("Document_Candidate");
    for (let k = 0; k < nodes.length; k++) {
        var element = nodes[k];
        let str=xmlDoc.getElementsByTagName("DocumentName")[0].textContent;
        arr.push(str);
    }
    return arr;
}

function ParsePhotoData(xmlDoc) {
    let arr=[];
    var nodes = xmlDoc.getElementsByTagName("Document_Image");
    for (let k = 0; k < nodes.length; k++) {
        var element = nodes[k];
        var imgString = `data:image/png;base64,${element.getElementsByTagName("File_Image")[0].textContent}`;
        arr.push({
            text:element.getElementsByTagName("FieldName")[0].textContent,
            src:imgString,
        })
    }
    return arr;
}

function ParseLexicalData(xmlDoc) {
    let arrAns = [];
    var hasMrz = 0, hasBarcode = 0, hasVisual = 0;
    let persent=0;
    let arr = xmlDoc.getElementsByTagName("Document_Field_Analysis_Info");
    for (let i = 0; i < arr.length; i++) {
        let element = arr[i];
        var fieldType = GetTranslation(getFromEnum(eVisualFieldType, element.getElementsByTagName("Type")[0].textContent));
        var lcid = element.getElementsByTagName("LCID")[0].textContent;
        if (lcid != 0)
            fieldType = fieldType + " (" + lcid + ")";
        var mrz = element.getElementsByTagName("Field_MRZ")[0].textContent;
        var barcode = element.getElementsByTagName("Field_Barcode")[0].textContent;
        var visual = element.getElementsByTagName("Field_Visual")[0].textContent;
        var mrzStat = element.getElementsByTagName("Matrix1")[0].textContent;
        var mrzStat = element.getElementsByTagName("Matrix1")[0].textContent;
        var mrzColor = mrzStat == eRPRM_FieldVerificationResult.RCF_Verified ? "Green" : mrzStat == eRPRM_FieldVerificationResult.RCF_Not_Verified ? "Red" : "Black";

        var barcodeStat = element.getElementsByTagName("Matrix4")[0].textContent;
        var barcodeColor = barcodeStat == eRPRM_FieldVerificationResult.RCF_Verified ? "Green" : barcodeStat == eRPRM_FieldVerificationResult.RCF_Not_Verified ? "Red" : "Black";

        var visualStat = element.getElementsByTagName("Matrix3")[0].textContent;
        var visualColor = visualStat == eRPRM_FieldVerificationResult.RCF_Verified ? "Green" : visualStat == eRPRM_FieldVerificationResult.RCF_Not_Verified ? "Red" : "Black";

        var mrzVisual = element.getElementsByTagName("Matrix6")[0].textContent;
        var mrzVisualColor = mrzVisual == eRPRM_FieldVerificationResult.RCF_Compare_True ? "Green" : mrzVisual == eRPRM_FieldVerificationResult.RCF_Compare_False ? "Red" : "Grey";

        var mrzBarcode = element.getElementsByTagName("Matrix7")[0].textContent;
        var mrzBarcodeColor = mrzBarcode == eRPRM_FieldVerificationResult.RCF_Compare_True ? "Green" : mrzBarcode == eRPRM_FieldVerificationResult.RCF_Compare_False ? "Red" : "Grey";

        var visualBarcode = element.getElementsByTagName("Matrix9")[0].textContent;
        var visualBarcodeColor = visualBarcode == eRPRM_FieldVerificationResult.RCF_Compare_True ? "Green" : visualBarcode == eRPRM_FieldVerificationResult.RCF_Compare_False ? "Red" : "Grey";
        var total = null;
        if (mrzStat == eRPRM_FieldVerificationResult.RCF_Not_Verified || barcodeStat == eRPRM_FieldVerificationResult.RCF_Not_Verified || visualStat == eRPRM_FieldVerificationResult.RCF_Not_Verified
            || mrzVisual == eRPRM_FieldVerificationResult.RCF_Compare_False || mrzBarcode == eRPRM_FieldVerificationResult.RCF_Compare_False || visualBarcode == eRPRM_FieldVerificationResult.RCF_Compare_False)
            total = false;
        else {
            var sum = mrzStat + barcodeStat + visualStat + mrzVisual + mrzBarcode + visualBarcode;
            if (sum > 0)
                total = true;
        }
        let ans = {
            "fieldType": fieldType,
            "mrz": mrz,
            "mrzColor": mrzColor,
            "barcode": barcode,
            "barcodeColor": barcodeColor,
            "visual": visual,
            "visualColor": visualColor,
            "mrzBarcodeColor": mrzBarcodeColor,
            "visualBarcodeColor": visualBarcodeColor,
            "mrzVisualColor":mrzVisualColor,
            "total":(total == true ? "Green" : total == false ? "Red" : "Grey"),
        };
        persent+=(ans["total"]==="Green"? 1 : ans["total"]==="Red" ? 0.5 :0);
        persent+=(ans["mrzVisualColor"]==="Green"? 1 : ans["mrzVisualColor"]==="Red" ? 0.5 :0);
        if (mrz != "")
            hasMrz = 1;
        if (barcode != "")
            hasBarcode = 1;
        if (visual != "")
            hasVisual = 1;
        arrAns.push(ans);
    }
    persent/=(arr.length*2);
    persent*=100;
    arrAns["persent"]=Math.ceil(persent);
    /*if (hasMrz == 1)
        $(".lexMrz").show();
    if (hasBarcode == 1)
        $(".lexBarcode").show();
    if (hasVisual == 1)
        $(".lexVisualOcr").show();
    if (hasMrz == 1 && hasVisual == 1)
        $(".lexMrzVisual").show();
    if (hasMrz == 1 && hasBarcode == 1)
        $(".lexMrzBarcode").show();
    if (hasVisual == 1 && hasBarcode == 1)
        $(".lexBarcodeVisual").show();*/
    return arrAns;
}




