import React from 'react';
import styled from 'styled-components'

const Li=styled.li`
  width:66px;
  height:96px;
  background-color:white;
  color:rgb(16,32,107);
  border:3px solid rgb(16,32,107);
  border-radius:5px;
  display:grid;
  grid-template-rows:60px 30px;
  ${Li}:hover{
    background-color:rgba(16,32,107,0.2);
  }
  transition:transform 0.15s;
`
const Img=styled.img`
    width:50px;
    height:50px;
    margin:auto;
    z-index:1000;
`

const Title=styled.div`
    width:60px;
    height:30px;
    color:rgb(16,32,107);
    font-weight:500;
    background-color:white;
    display:grid;
    justify-content:center;
    align-content:center;
    ${Li}:hover &{
        background-color:rgb(16,32,107);
        color:white;
    }
`

function aniMation(){
    let elem=document.getElementById(this.id);
    elem.style.transform="scale(1.05)";
    setTimeout(()=>{elem.style.transform="scale(1)";},150);
}

export default  function Button(props){
        return (
            <Li key={props.content} id={props.id} onClick={aniMation.bind({id:props.id,})}>
                <Img src={props.imageSource}/>
                <Title>{props.content}</Title>
            </Li>
        )
}


