import english from '../Languages/English';
import russian from '../Languages/Russian';

const languages=[
    english,
    russian,
]

export default function update(state={...english,languages},action){
    switch(action.type){
        case 'CHANGE_LANGUAGE':
            return {...languages.find((e)=>e.language===action.payload),languages};
    }
    return state;
}



