import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { Button, Form, FormGroup, Label, Input } from 'reactstrap';

const Wraper = styled.div`
    width:100vw;
    height:100vh;
    padding:10px;
    background-color:#fcfcfc;
    z-index:100;
    display:grid;
    grid-gap:5px;
    grid-template-columns:minmax(120px,700px) 320px;
    justify-content:space-around;
    overflow-y:auto;
    overflow-x:hidden;
`

const Wrap1 = styled.div`
    display:grid;
    align-content:center;
`

const Wrap3 = styled.div`
    height:700px;
    display:grid;
    align-content:top;
    justify-content:center;
`

const Wrap2 = styled.div`
height:365px;
width:312px;
padding:5px;
background-color:#fff;
box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
@media (max-height: 700px) {
  height:100%;
  width:100%;
  padding:5%;
  padding-top:25px;
}`

const Centrer = styled.div`
  width:100px;
  display: flex;
  align-items: center;
  justify-content: left;
`

const About = styled.div`
    max-width:700px;
    width:100%;
    height:700px;
    background-color:#fff;
    box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
    display:grid;
    grid-template-rows:45px 1fr;
`

const LabelReg = styled.div`
  height:45px;
  margin:-5px;
  margin-bottom:5px;
  padding-left:5px;
  color:white;
  font-size:30px;
  background-color:rgb(16,32,107);
  border-radius:3px;
  @media (max-height: 750px) {
    display:none;
  }
`

const AboutHeader=styled.div`
  width:100%;
  padding-left:5px;
  color:white;
  font-size:30px;
  border-radius:3px;
  background-color:rgb(16,32,107);
`
const AboutStaff=styled.div`
  width:100%;
  color:white;
`

class Client extends React.Component {
    constructor(props) {
        super(props);
        this.onSubmit = props.onSubmit;
        this.onLogOn = props.onLogOn;
    }
    render() {
        return (
            <Wraper>
                
                <Wrap1>
                    <About>
                        <AboutHeader>{this.props.store.language.clientPoss}</AboutHeader>
                        <AboutStaff></AboutStaff>
                    </About>
                </Wrap1>
                <Wrap1>
                    <Wrap3>
                        <Wrap2>
                            <LabelReg>{this.props.store.language.registration}</LabelReg>
                            <Form action='' row onSubmit={this.submitFunction}>
                                <FormGroup>
                                    <Label for="Firstname" hidden>{this.props.store.language.firstname}</Label>
                                    <Input type="text" name="First name" id="1Name" placeholder={this.props.store.language.firstname} />
                                </FormGroup>
                                <FormGroup>
                                    <Label for="Lastname" hidden>{this.props.store.language.lastname}</Label>
                                    <Input height="100px" type="text" name="Second name" id="2Name" placeholder={this.props.store.language.lastname}/>
                                </FormGroup>
                                <FormGroup>
                                    <Label for="Email" hidden>{this.props.store.language.email}</Label>
                                    <Input type="email" name="email" id="Email" placeholder={this.props.store.language.email} />
                                </FormGroup>
                                <FormGroup>
                                    <Label for="Password" hidden>{this.props.store.language.password}</Label>
                                    <Input type="password" name="password" id="Password" placeholder={this.props.store.language.password}/>
                                </FormGroup>
                                <FormGroup>
                                    <Label for="Passwordconfirm" hidden>{this.props.store.language.passwordConfirm}</Label>
                                    <Input type="password" name="passwordconfirm" id="Passwordconfirm" placeholder={this.props.store.language.passwordConfirm}/>
                                </FormGroup>
                                <Centrer><Input style={{ backgroundColor: 'rgb(16,32,107)', color: 'white' }} type='submit' color="success" value={this.props.store.language.submit} /></Centrer>
                            </Form>
                        </Wrap2>
                    </Wrap3>
                </Wrap1>
            </Wraper>
        )
    }
}
export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
    })
)(Client);


