import React from 'react';
import styled from 'styled-components';
import Logon from './Logon';

const Wraper=styled.div`
    box-shadow:0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
    z-index:100;
    border-radius:3px;
    display:grid;
    grid-template-columns:1fr;
    grid-template-rows:180px 370px 10px;
    background-color:rgb(16,32,107);
    padding:20px;
    background-image:url("${props=>props.src}");
    background-size:320px 600px;
    background-position: 0% 0%;
    background-repeat:no-repeat;
    transition:background-size 0.2s,background-position 0.2s;
    ${Wraper}:hover{
        background-size:427px 770px;
        background-position: 20% 20%;
    }
`

const Title=styled.div`
    width:100%;
    color:black;
    font-weight:bold;
    font-size:24px;
`

const Text=styled.div`
    width:100%;
    color:black;
    font-size:18px;
`
const Line=styled.div`
    width:0.1px;
    margin-left:-20px;
    transform:translate(0,200%);
    background-color:rgb(32,64,214);
    transition:width 0.2s;
    ${Wraper}:hover &{
        width:320px;
    }
`


export default function footerBlock(props) {
    return (
        <Wraper src={`${props.source}`}>
            <Title>{props.title}</Title>
            <Text>{props.text}</Text>
            <Line/>
        </Wraper>
    )
}


