import React from 'react';
import styled from 'styled-components';
import {connect} from 'react-redux';

const DropButton=styled.div`
    width:200px;
    font-size:20px;
    font-weight:500;
    color:white;
    height:30px;
    padding-left:5px;
    padding-right:30px;
    margin-top:18px;
    margin-bottom:18px;
    display:grid;
    justify-content:center;
    align-content:center;
    border:0;
    background-color:rgb(32,64,214);
    border-radius:2px;
    background-size: 30px 30px;
    background-image: url("https://www.thrivingparish.org/wp-content/uploads/2018/01/white-down-arrow-png-2.png");
    background-repeat: no-repeat;
    background-position: right center;
`
const DropDownComponent=styled.div`
    display:grid;
    width:150px;
    height:auto;
    grid-template-rows:30px 18px repeat(auto-fit,30px);
`
const DropItem=styled.li`
    width:100%;
    z-index:100;
    margin:0;
    border:1px solid grey;
    padding:5px;
    padding-right:30px;
    background-color:#fff;
    display:none;
    ${DropItem}:hover{
        background-color:#f5f5f5;
    }
    font-size:15px;
`


class DropDown extends React.Component{
    constructor(){
        super();
        this.drop=this.drop.bind(this);
        this.itemSelect=this.itemSelect.bind(this);
    }
    itemSelect(e){
        this.drop();
        this.props.onChangeLanguage(e.currentTarget.id);
    }
    drop(){
        let elem = document.getElementById("folderdrop");
        let items=document.getElementsByClassName("folderitem");
        if(items[0].style.display==="block"){
            elem.classList.add("dropup");
            elem.style.backgroundColor="rgb(45,80,255)";
            setTimeout(()=>{
                elem.classList.remove("dropup");
            }, 100);
            for(let i=0;i<items.length;i++){
                items[i].style.display="none";
            }
        }
        else{
            elem.classList.add("dropdown");
            elem.style.backgroundColor="rgb(32,64,214)";
            setTimeout(()=>{
                elem.classList.remove("dropdown");
            }, 100);
            for(let i=0;i<items.length;i++){
                items[i].style.display="block";
            }
        }      
    }
    render(){
        return(
            <DropDownComponent>
            <DropButton id="folderdrop" onClick={this.drop}>
                Язык/Language
            </DropButton>
            <div/>
            {this.props.store.language.languages.map((e)=>{
                return <DropItem id={e.language} key={e.language} className="folderitem" onClick={this.itemSelect}>{e.language}</DropItem>
            })}
            </DropDownComponent>           
        );
    }
}

export default connect(
    state =>({
      store:state,
    }),
    dispatch => ({
        onChangeLanguage: (data) => {
            dispatch({type:'CHANGE_LANGUAGE',payload:data});
        },
    })
)(DropDown);


