import styled from 'styled-components';

const Button=styled.button`
    border-radius:3.5px;
    outline:none;
    border:0px;
    height:30px;
    width:${props=>props.width || "100px"};
    font-size:20px;
    background-color:rgb(16,32,107);
    color:white;
    font-weight:500;
    display:grid;
    justify-content:center;
    align-content:center;
`

export default Button;


