let id = 0;
export default function update(state = [], action) {
    switch (action.type) {
        case 'PUSH':
            return state.concat({src: action.payload, id: id++,interpol:null,officer:null,data:null});
        case 'CLEAR':
            return [];
        case 'UPDATE_DOC':
            let ind=state.findIndex((e) => { return e.id === action.payload.id });
            var info=action.payload.data;
            var elem={...state[ind],data:info};
            var arr=[...state];
            arr[ind]=elem;
            return arr;
    }
    return state;
}


