import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import Button from './Button';

const Video = styled.video`
    width:427px;
    height:320px;
    display:none;
`

const Canvas = styled.canvas`
    width:427px;
    height:320px;
`

const Wraper = styled.div`
    margin:auto;
    position:absolute;
    width:427px;
    height:320px;
    display:grid;
    justify-content:center;
    align-content:center;
    background-color:white;
    z-index:1000000;
`

const Centrer = styled.div`
  display: flex;
  align-items: center;
  width:100px;
  justify-content: left;
`

const CameraBlock=styled.div`
    width:447px;
    height:410px;
    padding:10px;
    display:grid;
    justify-content:center;
    grid-gap:10px;
    grid-template-rows: 20px 320px 30px;
    border:3px solid rgb(32,64,214);
    background-color:white;
`
const Img=styled.img`
    z-index: 10;
    width: 428px;
    height: 322px;
    position:absolute;
    margin-left:-0.5px;
    margin-top:-0.5px;
`

const Header=styled.div`
    height:30px;
    width:calc(100% + 20px);
    background-color:rgb(32,64,214);
    margin-left:-10px;
    margin-top:-10px;
    display:grid;
    grid-template-columns:calc(100% - 30px) 30px;
    justify-content:center;
    align-content:center;
`

const Close=styled.img`
    width:20px;
    height:20px;
    margin-right:5px;
    margin-top:5px;
    border-radius:40%;
    background-color:red;
`

const HeaderTitle=styled.div`
    margin-left:10px;
    height:30px;
    color:white;
    font-weight:500;
    font-size:20px;
`

class VideoFlow extends React.Component {
    constructor() {
        super();
        this.stop=false;
        this.source;
    }
    componentDidMount() {
        this.video = document.querySelector("#video");
        this.canvas = document.querySelector('#myCanvas');
        this.canvas.width=427*2;
        this.canvas.height=320*2;
        this.ctx = this.canvas.getContext('2d');
        this.ctx.translate(427*2,0);
        this.ctx.scale(-1,1);
        let onCameraFail = function (e) {
            console.log('Camera did not work.', e); // Исключение на случай, если камера не работает
        };
        navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;
        window.URL = window.URL || window.webkitURL;
        navigator.getUserMedia({ video: true }, (stream)=>{
            this.stream=stream;
            this.video.src = window.URL.createObjectURL(stream);
        }, onCameraFail);
        this.cameraInterval = setInterval(snapshot.bind(this), 1);
        function snapshot() {
            if(!(this.stop)){
                this.source=this.video;
                this.ctx.drawImage(this.video, 0, 0,this.canvas.width,this.canvas.height);
            }
            else{
                this.props.onPush(this.canvas.toDataURL());
                document.getElementById("closeWebCam").click();
            }
        }
    }
    render() {
        return (
            <Wraper id="videoStream">
                <CameraBlock>
                    <Header>
                        <HeaderTitle>{this.props.store.language.doSnapshot}</HeaderTitle>
                        <Close id="closeWebCam" src="https://image.ibb.co/cRGRjH/mark_39951_640.png" onClick={()=>{
                            this.stream.getTracks()[0].stop();
                            document.getElementById("videoStream").style.display="none";
                            clearInterval(this.cameraInterval);
                            this.props.OFF();
                        }} title={this.props.store.language.close}/>
                    </Header>  
                    <div>
                    <Img src="https://image.ibb.co/eii4KH/pattern.png"/>
                    <Canvas id="myCanvas"/>
                    </div>
                    <Centrer><Button onClick={()=>{this.stop=!(this.stop)}}>{this.props.store.language.capture}</Button></Centrer>
                </CameraBlock>
                <Video id="video" controls autoplay />
            </Wraper>
        );
    }
}

export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
        OFF: () => {
            dispatch({ type: 'OFF' });
        },
        onPush: (data) => {
            dispatch({type:'PUSH',payload:data});
        },
    })
)(VideoFlow);


