import React from 'react';
import { Button, Form, FormGroup, Label, Input } from 'reactstrap';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';

const Wrap1 = styled.div`
    margin-left:calc(100% - 400px);
    transform:translate(0,100%);
    width:320px;
    margin-bottom:10px;
    @media (max-width: 500px) {
        width:100%;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:#fff;
    box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
    z-index:50;
`

const Wrap2 = styled.div`
    padding:5%;
    height:100%;
    width:100%;
`
const Centrer = styled.div`
  display: flex;
  align-items: center;
  width:70px;
  justify-content: left;
`
const LabelReg = styled.div`
    background-color:rgb(0, 121, 30);
    color:#fff;
    font-size:2em;
    font-weight:bold;
    border-radius:3px;
  `


class Logon extends React.Component {
    constructor(props) {
        super(props);
    }

    submitFunction(e) {
        e.preventDefault();
        /*let em = document.getElementById('EmailLogon').value.match(/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/);
        let pas = document.getElementById('PasswordLogon').value;
        if (em && pas) {
            this.onLogOn({
                email: em[0],
                password: pas,
            })
        }
        else {
            if (!em) {
                let el = document.getElementById('EmailLogon');
                let temp = el.value;
                el.value = "Please,enter correct email";
                el.style.color = "red";
                setTimeout(() => {
                    el.value = temp;
                    el.style.color = "black";
                }, 1000);
            }
            else {
                let el = document.getElementById('PasswordLogon');
                el.value = "Please,enter password";
                el.style.color = "red";
                setTimeout(() => {
                    el.value = "";
                    el.style.color = "black";
                }, 1000);
            }
        }*/
        window.location.replace('#/User');
    }
    render() {
        return (
            <Wrap1>
                <Wrap2>
                    <Form action='#' onSubmit={this.submitFunction.bind(this)}>
                        <FormGroup>
                            <Label for="Email" hidden>{this.props.store.language.email}</Label>
                            <Input type="email" name="email" id="EmailLogon" placeholder={this.props.store.language.email} />
                        </FormGroup>
                        <FormGroup>
                            <Label for="Password" hidden>{this.props.store.language.password}</Label>
                            <Input type="password" name="password" id="PasswordLogon" placeholder={this.props.store.language.password} />
                        </FormGroup>
                        <Centrer><Input style={{ backgroundColor: 'rgb(16,32,107)', color: 'white' }} type='submit' color="success" value={this.props.store.language.logOn} /></Centrer>
                    </Form>
                </Wrap2>
            </Wrap1>
        );
    }
    componentDidMount() {
        document.getElementById("EmailLogon").value = "Alfa_di@gmail.com";
        document.getElementById("PasswordLogon").value = "12345678";
    }
}
export default connect(
    state =>({
      store:state,
    }),
    dispatch => ({
    })
)(Logon);


