import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';

const Wraper = styled.div`
    width:calc(100% - 100px);
    height:calc(100% - 100px);
    background-color:rgba(16,32,107,0.3);
    display:grid;
    border:3px solid rgb(16,32,107);
    grid-gap:10px;
    max-height:900px;
    max-width:800px;
    overflow-y:auto;
    padding:5px;
`

const Col = styled.div`
    height:40px;
    font:20px;
    font-weight:500;
    width:100%;
    display:grid;
    grid-template-columns:1fr 1fr;
`
const Col1 = styled.div`
    height:100px;
    font:20px;
    font-weight:500;
    width:100%;
    display:grid;
    grid-template-columns:1fr 1fr;
`
const Square = styled.div`
    height:50px;
    font:20px;
    font-weight:500;
    width:100%;
    display:grid;
    grid-template-columns:150px 1fr 1fr 50px 50px;
    border-bottom:3px solid rgb(32,64,214);
`

const Table = styled.div`
    width:100%;
    display:grid;
    grid-template-rows:repeat(auto-fit,40px);
    border:3px solid rgb(32,64,214);
    background-color:white;
`
const TableText = Table.extend`
    grid-template-rows:repeat(auto-fit,40px);
`
const TableImg = Table.extend`
    grid-template-rows:repeat(auto-fit,100px);
`
const Cell = styled.div`
    display:grid;
    align-content:center;
    justify-content:center;
    border-left:3px solid rgb(32,64,214);
    font-weight:${props=> {return(props.weight || 600)}};
`

const Img = styled.img`
    width:30px;
    height:30px;
`

const Image = styled.img`
    max-width:170px;
    max-height:100px;
`

const RED = "https://image.ibb.co/hgn76T/Red.png";
const GREEN = "https://image.ibb.co/iW8x6T/Green.png";
const GREY = "https://image.ibb.co/hkRpLo/Grey.png";

function image(total) {
    return <Img src={total === "Green" ? GREEN : total === "Red" ? RED : GREY} />
}

class View extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <Wraper>
                <TableText>
                    {this.props.store.view && this.props.store.view.type && this.props.store.view.type.map((e, i) => {
                        return (<Col>
                            <Cell>{this.props.store.language.docType}</Cell>
                            <Cell>{e}</Cell>
                        </Col>)
                    })}
                </TableText>
                <TableImg>
                    {this.props.store.view && this.props.store.view.photo && this.props.store.view.photo.map((e, i) => {
                        return (<Col>
                            <Cell>{e.text}</Cell>
                            <Cell><Image src={e.src}></Image></Cell>
                        </Col>)
                    })}
                </TableImg>
                <TableText>
                    <Square>
                        <Cell weight="990">{this.props.store.language.fieldType}</Cell>
                        <Cell weight="990">{this.props.store.language.MRZ}</Cell>
                        <Cell weight="990">{this.props.store.language.Visual}</Cell>
                        <Cell weight="990">{this.props.store.language["MRZ-Visual"]}</Cell>
                        <Cell weight="990">{this.props.store.language.valid}</Cell>
                    </Square>
                    {this.props.store.view && this.props.store.view.lexical && this.props.store.view.lexical.map((e, i) => {
                        return (
                            <Square>
                                <Cell>{e.fieldType}</Cell>
                                <Cell>{e.mrz}</Cell>
                                <Cell>{e.visual}</Cell>
                                <Cell>{image(e.mrzVisualColor)}</Cell>
                                <Cell>{image(e.total)}</Cell>
                            </Square>)
                    })}
                </TableText>
            </Wraper>
        )
    }
}

export default connect(
    state => ({
        store: state,
    }),
    dispatch => ({
    })
)(View);
